* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.App {
  max-width: 1200px;
  padding-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 95vh;
  overflow-y: hidden;
  margin: 0px auto;
}

.link {
  text-decoration: none;
}
.link.link-start {
  color: white;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.button.start-button {
  background-color: #5dbea3;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  font-size: 3rem;
}
.button.pause-button {
  width: 300px;
  height: 50px;
  background-color: grey;
  border-radius: 10px;
  color: white;
}
.button.resume-button {
  width: 300px;
  height: 50px;
  background-color: grey;
  border-radius: 10px;
  color: white;
  background-color: #5dbea3;
}

.breathe {
  display: flex;
  flex-direction: column;
  width: 400px;
  align-items: center;
}
.breathe .message {
  display: flex;
  justify-content: center;
}
.breathe .progress {
  height: 500px;
  width: 83%;
  background: linear-gradient(0deg, rgb(93, 190, 163) 0%, rgb(221, 121, 115) 100%);
  margin-bottom: 20px;
}
.breathe .progress .slider {
  height: 500px;
  background-color: white;
  transition: height 5.5s;
}
.breathe .progress .slider.grow {
  height: 0px;
  transition: height 5.5s;
}

@media screen and (min-width: 1400px) {
  .App {
    width: 100vw;
  }
  .progress {
    width: 500px;
  }
}/*# sourceMappingURL=App.css.map */