@import 'colors.scss';
* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

@mixin pause-button {
  width: 300px;
  height: 50px;
  background-color: grey;
  border-radius: 10px;
  color: white;
}

.App {
  max-width: 1200px;
  padding-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 95vh;
  overflow-y: hidden;
  margin: 0px auto;
}

.link {
  text-decoration: none;

  &.link-start {
    color: white;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  &.start-button {
    background-color: $green;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    font-size: 3rem;
  }

  &.pause-button {
    @include pause-button;
  }

  &.resume-button {
    @include pause-button;
    background-color: $green;
  }
}

.breathe {
  display: flex;
  flex-direction: column;
  width: 400px;
  align-items: center;

  .message {
    display: flex;
    justify-content: center;
  }

  .progress {
    height: 500px;
    width: 83%;
    // border: 1px solid black;
    // border-radius: 10px;
    background: linear-gradient(
      0deg,
      rgba(93, 190, 163, 1) 0%,
      rgba(221, 121, 115, 1) 100%
    );
    margin-bottom: 20px;

    .slider {
      height: 500px;
      background-color: white;
      transition: height 5.5s;

      &.grow {
        height: 0px;
        transition: height 5.5s;
      }
    }
  }
}

@media screen and (min-width: 1400px) {
  .App {
    width: 100vw;
  }
  .progress {
    width: 500px;
  }
}
